import { useDispatch } from '@hooks'
import { InputAdornment, TextField, Tooltip } from '@mui/material'
import React, { useEffect, useMemo, useState, useRef } from 'react'
import { AnyObject, object, ValidationError, NumberSchema } from 'yup'
import {
  increaseInvalidFieldsNumber,
  decreaseInvalidFieldsNumber,
} from '@slices'

/**  `ValidatedTextField` */
export type ValidatedTextFieldProps = {
  /**   */
  label: string
  value?: string
  /**    */
  rule: NumberSchema<number, AnyObject>
  /**
   * , ,    
   * @param {number} value  
   */
  onValid(value: number): unknown

  /**       */
  adornment?: string

  endIconTooltip?: string
  EndIcon?: Icon
}

/**
 *    
 * @returns {JSX.Element}
 */
export const ValidatedTextField = ({
  label,
  rule,
  adornment,
  value,
  onValid,
  endIconTooltip,
  EndIcon,
}: ValidatedTextFieldProps) => {
  const dispatch = useDispatch()

  const [error, setError] = useState<string>()
  const inputRef = useRef<HTMLInputElement>(null)

  const schema = useMemo(
    () =>
      object({
        value: rule,
      }),
    [rule],
  )

  const handleChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = e.target.value

    schema
      .validate({
        value: newValue,
      })
      .then(({ value }) => {
        onValid(value)
        setError(undefined)
      })
      .catch((error: ValidationError) => {
        setError(error.message)
      })
  }

  useEffect(() => {
    if (inputRef.current) {
      inputRef.current.value = value ?? ''
    }

    setError(undefined)
  }, [value])

  useEffect(() => {
    if (error) dispatch(increaseInvalidFieldsNumber())
    else dispatch(decreaseInvalidFieldsNumber())
  }, [error, dispatch])

  return (
    <TextField
      inputRef={inputRef}
      label={label}
      error={!!error}
      helperText={error}
      onChange={handleChange}
      InputProps={{
        startAdornment: adornment && (
          <InputAdornment position='start'>{adornment}</InputAdornment>
        ),
        endAdornment: EndIcon && endIconTooltip && (
          <InputAdornment position='end'>
            <Tooltip title={endIconTooltip}>
              <EndIcon fontSize='small' />
            </Tooltip>
          </InputAdornment>
        ),
      }}
    />
  )
}
